/* eslint-disable default-case */
const initialState = {
  sortButtons: [
    { name: 'inexpensive', label: '–°–∞–º—ã–π –¥–µ—à–µ–≤—ã–π', isActive: true },
    { name: 'quick', label: '–°–∞–º—ã–π –±—ã—Å—Ç—Ä—ã–π', isActive: false },
    { name: 'optimal', label: '–û–ø—Ç–∏–º–∞–ª—å–Ω—ã–π', isActive: false },
  ],

  filter: [
    { label: '–í—Å–µ', name: 'all', isCheck: true },
    { label: '–ë–µ–∑ –ø–µ—Ä–µ—Å–∞–¥–æ–∫', name: '0', isCheck: true },
    { label: '1 –ø–µ—Ä–µ—Å–∞–¥–∫–∞', name: '1', isCheck: true },
    { label: '2 –ø–µ—Ä–µ—Å–∞–¥–∫–∏', name: '2', isCheck: true },
    { label: '3 –ø–µ—Ä–µ—Å–∞–¥–∫–∏', name: '3', isCheck: true },
  ],

  searchId: '',

  stopTickets: false,

  packetTickets: [],

  howManyTickets: 5,
};

const reduser = (state = initialState, action) => {
  switch (action.type) {
    case 'UPDATE_SEARCH_ID':
      return {
        ...state,
        searchId: action.payload,
      };
    case 'UPDATE_PACKET_TICKETS':
      return {
        ...state,
        packetTickets: [...state.packetTickets, ...action.payload],
      };
    case 'UPDATE_FILTER':
      return {
        ...state,
        filter: action.payload,
      };
    case 'UPDATE_STOP_TICKETS':
      return {
        ...state,
        stopTickets: action.payload,
      };
    case 'UPDATE_SORT_BUTTONS':
      return {
        ...state,
        sortButtons: action.payload,
      };
    case 'UPDATE_HOW_MANY_TICKETS':
      return {
        ...state,
        howManyTickets: state.howManyTickets + 5,
      };
  }
  return state;
};

export default reduser;
